import { HubConnection, HubConnectionBuilder } from "@microsoft/signalr";
import { BackgroundTask } from "models/api-models";
import { BackgroundTaskType } from "models/ui-models";
import React, {
	createContext,
	FC,
	useContext,
	useEffect,
	useState
} from "react";

interface BackgroundTaskState {
	libraryScan: BackgroundTask | null;
	seriesScan: BackgroundTask | null;
}

export const BackgroundTaskContext = createContext<BackgroundTaskState>({
	libraryScan: null,
	seriesScan: null,
});

export const useBackgroundTaskState = () => useContext(BackgroundTaskContext);

export const BackgroundTaskContextProvider: FC = (props) => {
	const [connection, setConnection] = useState<null | HubConnection>(null);
	const [libraryScan, setLibraryScan] = useState<BackgroundTask | null>(null);
	const [seriesScan, setSeriesScan] = useState<BackgroundTask | null>(null);

	useEffect(() => {
		const connect = new HubConnectionBuilder()
			.withUrl("/hubs/backgroundtasks")
			.configureLogging("trace")
			.withAutomaticReconnect()
			.build();
		setConnection(connect);
	}, []);

	const handleTaskStartedOrUpdated = (backgroundTask: BackgroundTask) => {
		console.log(backgroundTask);
		if (backgroundTask.type === BackgroundTaskType.LibraryScan) {
			setLibraryScan(backgroundTask);
		} else if (backgroundTask.type === BackgroundTaskType.SeriesScan) {
			setSeriesScan(backgroundTask);
		}
	};
	const handleTaskCompleted = (task: BackgroundTask) => {
		if (task.type === BackgroundTaskType.LibraryScan) {
			setLibraryScan(null);
		} else if (task.type === BackgroundTaskType.SeriesScan) {
			setSeriesScan(null);
		}
	};

	useEffect(() => {
		if (connection) {
			connection
				.start()
				.then(() => {
					connection.on("BackgroundTasks", (tasks) => {
						tasks?.map((task: BackgroundTask) =>
							handleTaskStartedOrUpdated(task)
						);
					});
					connection.on("BackgroundTaskStarted", (task) => {
						handleTaskStartedOrUpdated(task);
					});
					connection.on("BackgroundTaskUpdated", (task) => {
						handleTaskStartedOrUpdated(task);
					});
					connection.on("BackgroundTaskCompleted", (task) => {
						handleTaskCompleted(task);
					});
				})
				.catch((error) => console.log(error));
		}
	}, [connection]);

	return (
		<BackgroundTaskContext.Provider value={{ libraryScan, seriesScan }}>
			{props.children}
		</BackgroundTaskContext.Provider>
	);
};
